.navbar{
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu__bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.menu__bars:hover{
    cursor: pointer;
}

.nav__menu{
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    z-index: 1000;
    left: -100%;
    transition: 850ms;
}

.nav__menu.active{
    left: 0;
    transition: 350ms;
}

.nav__text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav__text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav__text .active {
    background-color: #1a83ff;
}

.nav__text a:hover{
    background-color: #1a83ff;
}

.nav__menu__items{
    width: 100%;   
}

.nav__toggle{
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left: 16px;
}